import React,  {Component} from 'react';
import { Row, Col, Typography  } from 'antd'

const style = {padding: '8px 30px'};
const { Title } = Typography;

class Forum extends Component {
  render() {
     return (
      <body>
      <div style={{textAlign: "center"}}>
         <Title level={1} >Communauté</Title>
         </div>
        <Row>
          <Col xs={{ span: 5, offset: 1 }} lg={{ span: 6, offset: 2 }}>
          <Title level={3} >Créez un compte discord</Title>
          <a href="https://discordapp.com/" target="_blank">
              < img src="data:image/png;base64," alt="sc2" style={{width:"80%", height:"80%"}}></img>
          </a>
          </Col>
          <Col xs={{ span: 11, offset: 1 }} lg={{ span: 6, offset: 2 }}>
          <Title level={3} >Rejoignez le serveur discord</Title>
          <a href="https://discord.gg/uY9gTF" target="_blank">
              < img src="https://github.com/GillesTimothy/timot/blob/master/discord-1200x630.png?raw=true" alt="sc2" style={{width:"80%", height:"80%"}}></img>
          </a>
        </Col>
          <Col xs={{ span: 5, offset: 1 }} lg={{ span: 6, offset: 2 }}>
          <Title level={3} >Acceptez Les règles </Title>
          <br></br>
          <p>Afin de trier les utilisateurs, il faut absolument <br></br>  approuvez les règles avant l'utilisation complète <br></br> du serveur discord. Un staff est disponnible <br></br> 7j/7 afin de vous aidez. Si vous êtes <br></br> actifs et sérieux, vous pourrez montez en grade <br></br> sur le serveur afin d'avoir plus de responsabilités.<br></br> Vous pourrez ainsi aidez les utilisateurs de <br></br>  la communauté si vous le souhaitez bien évidemment. </p>
          </Col>
        </Row>
        </body>

     );
  }
}

export default Forum;